class CommonAbstract {
  /**
   * @param editor 
   */
  constructor(editor) {
    this.editor = editor;
  }
  /**
   *  JQ 
   */


  get $toolbar() {
    return this.editor.$toolbar;
  }
  /**
   *  JQ 
   */


  get $container() {
    return this.editor.$container;
  }
  /**
   * 
   */


  get selection() {
    return this.editor.selection;
  }
  /**
   * 
   */


  get command() {
    return this.editor.command;
  }

}

export default CommonAbstract;