import { h } from 'hyperapp';
import './index.less';
import IconButton from '~/components/icon-button/view.jsx';
/**
 * @param item 
 * @param type question, topic, topics, user, users, article, relationships-user
 * @param primaryKey  relationships-user IDIDID
 * @param id  type  topicsusersrelationships-user
 * @param dataName  type  relationships-user 
 * @param actions
 */

export default (({
  item,
  type,
  primaryKey = null,
  id = null,
  dataName = null,
  actions
}) => {
  const {
    is_following
  } = item.relationships;
  return h(IconButton, {
    cls: "mc-follow",
    icon: "star_border",
    iconActive: "star",
    tooltip: is_following ? '' : '',
    active: is_following,
    onClick: () => actions.toggleFollow({
      type,
      dataName,
      primaryKey,
      id
    })
  });
});