import timestamp from 'time-stamp';
/**
 * 
 * @param ts
 * @param format
 */

export function timeFormat(ts, format = 'YYYY-MM-DD HH:mm:ss') {
  const date = new Date(ts * 1000);
  return timestamp(format, date);
}
/**
 * 
 * @param ts
 */

export function timeFriendly(ts) {
  // 
  const date = new Date(ts * 1000); // 

  const currentTS = Date.parse(new Date().toString()) / 1000; // 

  const timeDifference = currentTS - ts;

  if (timeDifference < 1) {
    return '';
  }

  if (timeDifference < 60) {
    return `${timeDifference} `;
  }

  if (timeDifference < 3600) {
    return `${parseInt(String(timeDifference / 60), 10)} `;
  } //  0 


  const todayTS = Date.parse(new Date(new Date().setHours(0, 0, 0, 0)).toString()) / 1000;

  if (ts > todayTS) {
    return ` ${timestamp('HH:mm', date)}`;
  } //  0 


  const yearTS = Date.parse(new Date(`${new Date().getFullYear()}/01/01 00:00:00`).toString()) / 1000;

  if (ts > yearTS) {
    return timestamp('MM-DD HH:mm', date);
  }

  return timestamp('YYYY-MM-DD', date);
}
/**
 * 
 */

export function currentTimestamp() {
  return Date.parse(new Date().toString()) / 1000;
}