export const tabs = ['following', 'recommended'];
const state = {
  //  followingrecommended
  current_tab: '',
  // 
  tabs,
  // 
  tabIndex: tabs.indexOf('recommended')
};
tabs.forEach(tabName => {
  state[`${tabName}_data`] = [];
  state[`${tabName}_pagination`] = null; //  null 

  state[`${tabName}_loading`] = false;
});
export default state;