import param from 'mdui.jq/es/functions/param';
/**
 *  url  queryParam
 * @param path              url
 * @param params            path  query requestBody 
 * @param queryParamNames  query 
 */

export function buildURL(path, params = {}, queryParamNames = []) {
  //  path 
  const url = path.replace(/({.*?})/g, match => {
    const pathParamName = match.substr(1, match.length - 2);

    if (params[pathParamName] == null) {
      throw new Error(`Missing required parameter ${pathParamName}`);
    }

    return String(params[pathParamName]);
  }); //  query 

  const queryObj = {};
  queryParamNames.forEach(name => {
    if (params[name] != null) {
      queryObj[name] = String(params[name]);
    }
  });
  const queryString = param(queryObj);
  return queryString ? `${url}?${queryString}` : url;
}
/**
 *  requestBody 
 * @param params            path  query requestBody 
 * @param requestBodyNames requestBody 
 */

export function buildRequestBody(params, requestBodyNames) {
  const requestBody = {};
  requestBodyNames.forEach(name => {
    if (params[name] != null) {
      requestBody[name] = params[name];
    }
  });
  return requestBody;
}