import { getRequest, postRequest, deleteRequest } from './util/requestAlias';
import { buildURL } from './util/requestHandler';
/**
 * 
 *
 * 
 */

export const del = params => deleteRequest(buildURL('/notifications/{notification_id}', params));
/**
 * 
 *
 * 
 */

export const deleteAll = (params = {}) => deleteRequest(buildURL('/notifications', params, ['type']));
/**
 * 
 *
 * 
 */

export const deleteMultiple = params => deleteRequest(buildURL('/notifications/{notification_ids}', params));
/**
 * 
 *
 * 
 */

export const getCount = (params = {}) => getRequest(buildURL('/notifications/count', params, ['type']));
/**
 * 
 *
 * 
 */

export const getList = (params = {}) => getRequest(buildURL('/notifications', params, ['page', 'per_page', 'include', 'type', 'read']));
/**
 * 
 *
 * 
 */

export const read = params => postRequest(buildURL('/notifications/{notification_id}/read', params, ['include']));
/**
 * 
 *
 * 
 */

export const readAll = (params = {}) => postRequest(buildURL('/notifications/read', params, ['type']));
/**
 * 
 *
 * 
 */

export const readMultiple = params => postRequest(buildURL('/notifications/{notification_ids}/read', params, ['include']));