import $ from 'mdui.jq/es/$';
import 'mdui.jq/es/methods/attr';
/**
 *  DATA API 
 * @param element 
 * @param name 
 */

function parseOptions(element, name) {
  const attr = $(element).attr(name);

  if (!attr) {
    return {};
  }

  return new Function('', `var json = ${attr}; return JSON.parse(JSON.stringify(json));`)();
}

export { parseOptions };