import $ from 'mdui.jq/es/$';
import 'mdui.jq/es/methods/appendTo';
import 'mdui.jq/es/methods/insertAfter';
import 'mdui.jq/es/methods/remove';
import CommonAbstract from '../abstracts/commonAbstract';
/**
 *  document.execCommand 
 */

class Command extends CommonAbstract {
  /**
   * 
   * @param name
   * @param value
   */
  do(name, value) {
    // 
    if (!this.selection.getRange()) {
      return;
    } // 


    this.selection.restore();
    const customName = name; // 
    // @ts-ignore

    if (this[customName]) {
      // @ts-ignore
      this[customName](value);
    } else {
      document.execCommand(name, false, value);
    } // 


    this.editor.menus.changeStatus(); // 

    this.selection.saveRange();
    this.selection.restore(); //  onchange

    if (this.editor.change) {
      this.editor.change();
    }
  }
  /**
   *  insertHTML  HTML
   * @param html
   */
  // @ts-ignore


  insertHTML(html) {
    // W3C
    if (document.queryCommandSupported('insertHTML')) {
      document.execCommand('insertHTML', false, html);
      return;
    }

    const range = this.selection.getRange();

    if (range.insertNode) {
      // IE
      range.deleteContents();
      range.insertNode($(html)[0]); // @ts-ignore
    } else if (range.pasteHTML) {
      // IE <= 10
      // @ts-ignore
      range.pasteHTML(html);
    }
  }
  /**
   *  HTML  root 
   * @param html
   */
  // @ts-ignore


  replaceRoot(html) {
    const $oldElem = this.selection.getRootElem();
    const $newElem = $(html).insertAfter($oldElem);
    $oldElem.remove();
    this.selection.createRangeByElem($newElem, false, true);
    this.selection.restore();
  }
  /**
   *  root  html
   * @param html
   */
  // @ts-ignore


  insertAfterRoot(html) {
    const $oldElem = this.selection.getRootElem();
    const $newElem = $(html).insertAfter($oldElem);
    this.selection.createRangeByElem($newElem, false, true);
    this.selection.restore();
  }
  /**
   *  $content  html
   * @param html
   */
  // @ts-ignore


  appendHTML(html) {
    const $newElem = $(html).appendTo(this.$container);
    this.selection.createRangeByElem($newElem, false, true);
    this.selection.restore();
  }
  /**
   *  elem
   * @param $elem
   */
  // @ts-ignore


  insertElem($elem) {
    const range = this.selection.getRange();

    if (range.insertNode) {
      range.deleteContents();
      range.insertNode($elem[0]);
    }
  }

}

export default Command;